%link-style{
    color: #0d6efd;
    cursor: pointer;
}

*,
::before,
::after{
    box-sizing: border-box;
    scroll-behavior: smooth;
}
body {
    margin: 0;
}
a{
    text-decoration: none;
}
.hidden{
    display: none!important;
}
.material-symbols-outlined{
    vertical-align: middle;
  font-variation-settings:
  'FILL' 0,
  'wght' 400,
  'GRAD' 0,
  'opsz' 48;
}
